import React, { useState, useRef, useEffect } from 'react';
import { getChatSource } from '../utils/source';
import moment from 'moment';
import './index.css';

function debounce(func, wait) {
    var timeout;
    return function () {
        clearTimeout(timeout)
        timeout = setTimeout(func, wait);
    }
}

/**
* call function within Transaction
* @param {Function} onButtonClick 点击send,非空时发送数据,清空输入框
* @param {Function} onEnterKey 输入框enter发送数据,其它同上
* @param {Function} computedData 滚动时根据index获取需要展示的数据,默认滚动到头部时加载5条数据
*/
function HomePage() {
    const data = getChatSource();
    const [messages, setMessages] = useState(data.slice(-5));
    const [fistItemindex, setIndex] = useState(messages[0].index);
    const inputEl = useRef(null);
    console.log('1000')
    const onButtonClick = () => {
        inputEl.current.focus();
        let currentTime = new Date().getTime() - 10000000;
        if(inputEl.current.value.trim() !== ''){
            setMessages(messages.concat({
                time:moment(currentTime).format("HH:MM"), 
                msg: `${inputEl.current.value}`, 
                tag:'self'})
            );
        }
        inputEl.current.value = '';
    };

    const onEnterKey = (e) => {
        if(e.nativeEvent.keyCode === 13){
            onButtonClick();
        }
    };

    const computedData = () => {
        console.log('1')
        if(window.scrollY === 0){
            setMessages(data.slice(fistItemindex - 5,fistItemindex).concat(messages));
            setIndex(fistItemindex - 5);
        }
    }

    useEffect(() => {
        const getData = debounce(computedData,1000);
        console.log('1')
        window.addEventListener('scroll', getData, false)
        return () => {
          window.removeEventListener('scroll', getData, false)
        }
    })
    
    return (
        <div>
            <header className="header-wrapper">
                <span className="header-action">&lt;</span>
                <div className="header-avatar">
                    <img src='https://static.it1352.com/Content/img/login_register.png' alt="avatar" />Johnson
                </div>
                <span className="header-action">...</span>
            </header>
            <div className="dialog-list">
                {
                    messages.map((item, index) => 
                        <div key={index.toString()} className={`dialog-item ${item.tag ? 'self-message': ''}`}>
                            <div className="dialog-time">{item.time}</div>
                            <div className="dialog-text">{item.msg}</div>
                        </div>
                    )
                }
            </div>

            <footer className="footer-wrapper">
                <input ref={inputEl} type="text" onKeyPress={onEnterKey} className="footer-input" placeholder="Message" />
                <button onClick={onButtonClick} className="footer-button">Send</button>
            </footer>
        </div>
    );
}

export default HomePage;