import moment from 'moment';

export const getChatSource = () => {
    const results = [];
    let currentTime = new Date().getTime() - 10000000;
    for(let i=0;i<100;i++) {
        results.push({time: moment(currentTime).format("HH:MM"), msg: `Message "${i}`, index:i});
        currentTime++;
    }

    return results;
}